.card, 
.card-body {
    background: #0000;
}

.card {
    border: 1px #888 solid;
    border-radius: 15px;
}

.site-btn:hover,
.repo-btn:hover {
    animation: reflejo 0.5s;
}


@keyframes reflejo {
    0% {
        background: linear-gradient(
            45deg,
            #00000000 0%,
            #00000000 1%,
            #ffffffff 5%,
            #00000000 10%,
            #00000000 100%
        );
    }
    5% {
        background: linear-gradient(
            45deg,
            #00000000 0%,
            #00000000 5%,
            #ffffffff 10%,
            #00000000 15%,
            #00000000 100%
        );
    }
